.left-section{
    background-color: #1c1b23;
    width: 18%;
    height:100%;
    padding: 5px;
    box-sizing: border-box;
    overflow-y: hidden;
    overflow-y: scroll;
}

.left-section::-webkit-scrollbar{
    width: 7px;
}
.left-section::-webkit-scrollbar-thumb{
    background: #0a090d;;
}